<template>
  <div class="arrow" @click="goTo">
    <div v-animateCssHover="'bounceIn'">
      <div>
        <p>{{text}}</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "arrow",
  props: ["text", "link"],
  methods: {
    goTo() {
      this.$store.getters.typeTransition = "slide";
      setTimeout(() => {
        this.$router.push(this.link);
        setTimeout(() => {
          this.$store.getters.typeTransition = "fade";
        }, 1200);
      }, 10);
    }
  }
};
</script>

<style lang="scss">
@import "./../assets/scss/_portfolioFonts";
@import "./../assets/scss/_portfolioVars";
.arrow {
  display: flex;
  margin: 0 auto;
  flex-direction: column;
  align-items: center;
  position: relative;
  text-transform: uppercase;
  z-index: 25;
  & div > div {
    cursor: pointer;
    display: flex;
    text-decoration: none;
  }
  & svg {
    transform: scale(0.7);
    fill: white;
    margin: 0 auto;
    margin-bottom: 20px;
  }
  & > div > div > p {
    font-family: $g_black;
    letter-spacing: calc(20px * -50 / 1000);
    font-size: 20px;
    color: white;
  }
  & > div:hover svg {
    fill: $pinc;
  }
  & > div:hover p {
    color: $pinc;
  }
}
</style>