<template>
  <nav class="c-menu">
    <div
      @click="show=!show"
      class="contIcons"
      style="transition: transform .5s linear"
      :style="show ? 'transform:rotateY(180deg)':'transform:rotateY(0)'"
    >
      <font-awesome-icon icon="times" class="iconClose" />
      <span :class="show? 'span-active':''">menu</span>
    </div>

    <transition
      v-if="show"
      appear
      appear-active-class="animate__animated animate__fadeInLeft"
      leave-active-class="animate__animated animate__fadeOutRight"
    >
      <ul>
        <router-link v-for="field in fields" :to="{name:field.name}" :key="field.name" tag="li">
          <a v-if="fieldName!= field.name" @click.prevent="hideMenu(field.name)">{{field.title}}</a>
        </router-link>
      </ul>
    </transition>
  </nav>
</template>

<script>
import "./../../node_modules/animate.css/source/fading_entrances/fadeInLeft.css";
import "./../../node_modules/animate.css/source/fading_exits/fadeOutRight.css";
import { mapGetters } from "vuex";

export default {
  name: "menunnav",
  props: {
    fieldName: {
      type: String,
      required: true
    }
  },

  data() {
    return {
      show: false
    };
  },

  computed: mapGetters({ fields: "menu/getMenuFields" }),

  methods: {
    hideMenu(name) {
      this.show = !this.show;
      this.$router.push({ name: name });
    }
  }
};
</script>

<style lang="scss">
@import "./../assets/scss/_portfolioFonts";
@import "./../assets/scss/_portfolioVars";

$duration: 0.5s;

@keyframes rotateRight {
  from {
    transform: rotateY(0);
  }
  to {
    transform: rotateY(180deg);
  }
}

.rotateRight rotateRight {
  margin-top: 400px;
  animation: rotateRight $duration linear forwards;
}
.rotateLeft rotateRight {
  animation: rotateRight $duration linear forwards;
}

.c-menu {
  margin-left: auto;
  display: flex;
  z-index: 5;
  position: relative;
  flex-direction: column;
  justify-content: center;
  font-family: $g;
  & > ul {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    position: absolute;
    top: 55px;
    right: 0;
    width: 170px;
    background-color: black;
    padding: 10px;
    border-radius: 5px;
    -webkit-box-shadow: 0px 0px 2px 2px rgba(173, 133, 173, 1);
    -moz-box-shadow: 0px 0px 2px 2px rgba(173, 133, 173, 1);
    box-shadow: 0px 0px 2px 2px rgba(173, 133, 173, 1);
  }
  & > ul > li {
    list-style-type: none;
    text-align: right;

    & > a {
      display: flex;
      cursor: pointer;
      text-decoration: none;
      color: white;
      font-size: 25px;
      font-family: $g_bold;
      padding: 15px 0;
      letter-spacing: calc(25px * -25 / 1000);
      justify-content: flex-end;
      &:hover {
        color: $pinc;
      }
      &:active {
        color: $pincHover;
      }
    }
  }
  & > ul > li:last-of-type {
    margin-bottom: 0;
  }
  & .contIcons {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    transform: rotateY(0);
    transform-style: preserve-3d;
  }

  & .contIcons > .iconClose {
    font-weight: 200;
    position: absolute;
    left: 20px;
    top: -2px;
    font-size: 30px;
    color: white;
    backface-visibility: hidden;
    transform: rotateY(180deg);
    cursor: pointer;
    &:hover {
      color: $pinc;
    }
    &:active {
      color: $pincActive;
    }
  }
  & .contIcons > span {
    cursor: pointer;
    backface-visibility: hidden;
    font-family: $g_bold;
    margin-left: auto;
    display: block;
    color: white;
    font-size: 20px;
    position: relative;
    text-transform: uppercase;
    letter-spacing: calc(20px * -25 / 1000);
    &::before {
      content: "";
      display: block;
      backface-visibility: hidden;
      width: 140%;
      border-top: 1px solid;
      border-color: white;
      transition: border-color 500ms;
      position: absolute;
      top: 50%;
      right: -11px;
    }
    &:hover {
      color: $pincHover;
    }
    &:active {
      color: $pincActive;
    }
    &:hover:before {
      border-color: $pincHover;
      transition: border-color 500ms;
    }
    &:active:before {
      border-color: $pincHover;
      transition: border-color 500ms;
    }
    &.span-active {
      font-family: $g_bold;
      color: $pinc;
    }
    &.span-active::before {
      content: "";
      display: block;
      width: 140%;
      backface-visibility: hidden;
      transition: border-color 500ms;
      border-top: 1px solid;
      border-color: $pinc;
      position: absolute;
      transform: rotateY(180deg);
      top: 50%;
      right: -11px;
    }
  }
}
</style>