export default {
    namespaced: true,
    state: {
        validFields: [
            {
                name: "тема",
                valid: false,
                pattern: /^[a-zа-яё\s]{2,}/iu,
                value: "",
                required: false
            },
            {
                name: "Имя",
                valid: false,
                pattern: /^[a-zа-яё\s]{2,}/iu,
                value: "",
                required: true
            },
            {
                name: "e-mail",
                valid: false,
                pattern: /^([a-z0-9_-]+\.)*[a-z0-9_-]+@[a-z0-9_-]+(\.[a-z0-9_-]+)*\.[a-z]{2,6}$/,
                value: "",
                required: true
            },
            {
                name: "телефон",
                valid: false,
                pattern: /^(\s*)?(\+)?([- _():=+]?\d[- _():=+]?){10,14}(\s*)?$/,
                value: "",
                required: true
            },
        ]
    },
    getters: {
        validFields: state => state.validFields,
        resaltValid: state => { 
            let res = true
            state.validFields.forEach(el => {
                if (el.required && !el.pattern.test(el.value)) { 
                    res = false
                }  
            });
            console.log(res)
            return res
        }
    }
}