<template>
  <div id="app">
    <transition enter-active-class="enterEl" leave-active-class="leaveEl" mode="out-in">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </transition>
  </div>
</template>

<script>
export default {
  name: "App"
};
</script>

<style lang="scss">
@keyframes a-enterEl {
  from {
    transform-origin: left 50vw top 10vh;
    transform: scale(0);
    opacity: 0.3;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}
@keyframes a-leaveEl {
  from {
    transform-origin: left 50vw top 10vh;
    transform: scale(1);
    opacity: 1;
  }
  to {
    opacity: 0.3;
    transform: scale(0);
  }
}

$duration: 0.5s;

.enterEl {
  animation: a-enterEl $duration;
}

.leaveEl {
  animation: a-leaveEl $duration;
}

body {
  margin: 0;
  background-color: gray;
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: white;
}
</style>
