
class myDirectives {
    install(Vue) {

        require('animate.css')

        Vue.directive('showInScroll', {
            inserted(el, binding) {
                el.classList.add("animate__animated");
                el.style.opacity = '0'
                window.addEventListener("scroll", () => {
                    if ((el.offsetTop * 40) / 100 < window.scrollY) {
                        el.style.opacity = '1'
                        if (typeof (binding.value) == 'string') {
                            el.classList.add(`animate__${binding.value}`);
                        } else {
                            el.classList.add(`animate__${binding.value.anim}`)
                            if (binding.value.delay) {
                                el.style.animationDelay = `${binding.value.delay}`
                            }
                            if (binding.value.duration) {
                                el.style.animationDuration = `${binding.value.duration}`
                            }
                        }
                    }
                });
            }
        })

        Vue.directive('animateCss',
            {
                bind(el) {
                    el.classList.add("animate__animated");
                },
                inserted(el, binding) {
                    if (typeof (binding.value) == 'string') {
                        el.classList.add(`animate__${binding.value}`);
                    } else {
                        el.classList.add(`animate__${binding.value.anim}`)
                        if (binding.value.delay) {
                            el.style.animationDelay = `${binding.value.delay}`
                        }
                        if (binding.value.duration) {
                            el.style.animationDuration = `${binding.value.duration}`
                        }
                    }
                }
            })



        Vue.directive('animateCssHover', {
            inserted(el, binding) {
                let delay = 0
                let duration = 500
                el.classList.add("animate__animated");
                if (binding.value.delay) {
                    el.style.animationDelay = `${binding.value.delay}`
                    delay = parseInt(binding.value.delay.match(/\d+/))
                }
                if (binding.value.duration) {
                    el.style.animationDuration = `${binding.value.duration}`
                    duration = parseInt(binding.value.duration.match(/\d+/))
                }
                el.addEventListener('mouseover', () => {
                    if (typeof (binding.value) == 'string') {
                        el.classList.add(`animate__${binding.value}`);
                        setTimeout(() => el.classList.remove(`animate__${binding.value}`), delay + duration)
                    } else {
                        el.classList.add(`animate__${binding.value.anim}`)
                        setTimeout(() => el.classList.remove(`animate__${binding.value.anim}`), delay + duration)
                    }
                })
            }
        })
        Vue.directive('animateCssClick', {
            inserted(el, binding) {
                let delay = 0
                let duration = 500
                el.classList.add("animate__animated");
                if (binding.value.delay) {
                    el.style.animationDelay = `${binding.value.delay}`
                    delay = parseInt(binding.value.delay.match(/\d+/))
                }
                if (binding.value.duration) {
                    el.style.animationDuration = `${binding.value.duration}`
                    duration = parseInt(binding.value.duration.match(/\d+/))
                }
                el.addEventListener('click', () => {
                    if (typeof (binding.value) == 'string') {
                        el.classList.add(`animate__${binding.value}`);
                        setTimeout(() => el.classList.remove(`animate__${binding.value}`), delay + duration)
                    } else {
                        el.classList.add(`animate__${binding.value.anim}`)
                        setTimeout(() => el.classList.remove(`animate__${binding.value.anim}`), delay + duration)
                    }
                })
            }
        })
    }
}
export default new myDirectives