<template>
  <div class="E_examplCont">
    <img :src="el.link_img" :alt="el.name" width="300" height="280" />
    <p class="E_tegs">Теги: {{c_tegs(el.tegs)}}</p>
    <details>
      <summary>Посмотреть описание</summary>
      <p class="E_description">{{el.desc}}</p>
      <a :href="el.link" target="_blank">Посмотреть проект</a>
      <a :href="el.link_git" target="_blank">Git репозиторий</a>
    </details>
  </div>
</template>

<script>
export default {
  name: "example",
  props: ["el"],
  methods: {
    c_tegs(el) {
      let str = "";
      el.forEach((e, i) => {
        if (i != el.length - 1) {
          str += e + ", ";
        } else {
          str += e;
        }
      });
      return str;
    }
  }
};
</script>


<style lang="scss">
@import "./../assets/scss/_portfolioFonts";
@import "./../assets/scss/_portfolioVars";
.E_examplCont {
  width: 30%;
  padding: 10px;
  margin: 0 5px 10px 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: background-color 500ms;
  padding: 20px;
  box-sizing: border-box;
  border-radius: 0px 20px 0 20px;
  background-color: rgba(32, 32, 32, 0.705);
  &:hover {
    background-color:  rgb(32, 32, 32);
  }

  & > img {
    margin: 20px 0;
    border-radius: 2px;
    -webkit-box-shadow: 0px 0px 5px 0px rgba(247, 244, 247, 1);
    -moz-box-shadow: 0px 0px 5px 0px rgba(247, 244, 247, 1);
    box-shadow: 0px 0px 5px 0px rgba(247, 244, 247, 1);
  }
  & > h5 {
    width: 300px;
    display: flex;
    text-align: left;
    font-size: 20px;
    margin: 10px 0;
  }
  & details {
    width: 300px;
    position: relative;
  }
  & summary {
    width: 100%;
    display: flex;
    text-align: left;
    cursor: pointer;
  }
  & summary:focus {
    outline: none; 
  }
  & summary::-webkit-details-marker {
    display: none;
  }

  & summary::after {
    position: absolute;
    content: "\25B6";
    padding-right: 0.5em;
    left:190px ;
    top:1px;
  }
  & details[open] > summary {
      color:$pinc;
  }
  & details[open] > summary::after {
    position: absolute;
    content: "\25BC";
    left:190px ;
    top:1px;
    color: $pinc;
  }

  & p {
    width: 300px;
    padding: 5px;
    text-align: justify;
    margin: 10px 0;
  }
  & a {
    padding: 5px;
    display: flex;
    text-decoration: none;
    color: white;
    font-style: italic;
    &:hover {
      color: $pincHover;
    }
  }
}
</style>