<script>
import contener from "./../components/contener";
import addText from "./../components/addText";
import MpfHeader from "./../components/header";
import MpfMenu from "./../components/menu";
import MpfValidField from "./../components/validField";
import { mapGetters } from "vuex";

export default {
  name: "contacts",
  components: {
    contener,
    addText,
    MpfHeader,
    MpfMenu,
    MpfValidField
  },
  data() {
    return {};
  },
  computed: {
 
    getButtonClass() {
      return this.valid ? "mycontacts__button" : "mycontacts__button-disabled";
    },
    ...mapGetters({ fields: "contacts/validFields", valid: "contacts/resaltValid" }),
