<template>
  <label class="valid-field">
    <input
      type="text "
      class="mycontacts__name"
      :placeholder="field.name"
      v-model="field.value"
      :class="active ? activeClass: ''"
    />
    <span v-if="field.required">*</span>
  </label>
</template>

<script>
export default {
  props: {
    field: {
      type: Object,
      required: true
    }
  },
  computed: {
    active() {
      return this.field.value != "";
    },
    valid() {
      return this.field.pattern.test(this.field.value);
    },
    activeClass() {
      return this.valid ? "validTrue" : "validFalse";
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../assets/scss/_portfolioFonts";
@import "./../assets/scss/_portfolioVars";
.valid-field {
  display: flex;
}
.valid-field span {
  color: $pinc;
}

.validTrue {
  border-color: rgb(137, 228, 137) !important;
}
.validFalse {
  border-color: $pinc !important;
}

.valid-field input {
  width: 100%;
  height: 40px;
  background: url(./../assets/img/fon_field.png) no-repeat;
  background-size: cover;
  background-position: center;
  border: 1px solid #282828;
  outline: none;
  margin-bottom: 15px;
  padding-left: 10px;
  font-family: $g;
  font-size: 24px;
  color: #f3f3f3;
  -webkit-box-shadow: inset 3px 3px 8px -2px rgba(0, 0, 0, 0.49);
  -moz-box-shadow: inset 3px 3px 8px -2px rgba(0, 0, 0, 0.49);
  box-shadow: inset 3px 3px 8px -2px rgba(0, 0, 0, 0.49);
  @include placeholder {
    font-family: $g;
    font-size: 24px;
  }
  &:hover {
    border: 1px solid pink;
    -webkit-box-shadow: inset 3px 3px 8px -2px rgba(135, 135, 135, 1);
    -moz-box-shadow: inset 3px 3px 8px -2px rgba(135, 135, 135, 1);
    box-shadow: inset 3px 3px 8px -2px rgba(135, 135, 135, 1);
  }
  &:focus {
    border: 1px solid pink;
    -webkit-box-shadow: inset 3px 3px 8px -2px rgba(135, 135, 135, 1);
    -moz-box-shadow: inset 3px 3px 8px -2px rgba(135, 135, 135, 1);
    box-shadow: inset 3px 3px 8px -2px rgba(135, 135, 135, 1);
  }
}
</style>