<template>
  <contener namePage="myWorks" bc="transparent">
    <mpf-header>
      <mpf-menu fieldName="MyWorks" />
    </mpf-header>
    <h3 class="myWorks__title">Мои работы</h3>
    <addText>
      <p>Примеры моих работ</p>
    </addText>
    <section class="bodyMyWorks">
      <div class="wrepper">
        <example
          v-for="(exm,i) in works"
          :key="i"
          v-bind:el="exm"
          v-animateCss="{anim:'fadeInUp', delay: `${i*200+300}ms`, duration: '1000ms'}"
        />
      </div>
    </section>
    <arrow text="Отзывы" link="#Reviews"></arrow>
  </contener>
</template>
<script>
import contener from "./../components/contener";
import arrow from "./../components/arrow";
import MpfHeader from "./../components/header";
import example from "./../components/example";
import addText from "./../components/addText";
import MpfMenu from "./../components/menu"

export default {
  name: "myWorks",
  components: { contener, arrow, MpfHeader, example, addText,  MpfMenu  },
  computed: {
    works() {
      return this.$store.getters.getWorks;
    }
  }
};
</script>

<style lang="scss" >
@import "./../assets/scss/_portfolioFonts";
@import "./../assets/scss/_portfolioVars";
.myWorks__title {
  font-family: $g_bold;
  margin: 30px 0 0 0;
  font-size: 50px;
}
.bodyMyWorks {
  margin-top: 30px;
  display: flex;
  width: 100%;
  justify-content: space-around;
}
.wrepper {
  padding: 20px;
  width: calc(100% - 250px);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
</style>